import listMixin from '@/mixin/List.js'
export default {
	name: 'message',
	mixins: [listMixin],
	methods: {
	getData () {
		this.$http(this.$urlPath.messageList, {
			page: this.page.num
		}, null, (data) => {
			this.loadSuccess(data.data)
		}, (errorCode, error) => {
			this.loadError()
		}, () => {
			uni.stopPullDownRefresh()
		})
	},
	itemClick (item) {
		item.is_read = true
		this.$push('/pages/message/MessageInfo?mid=' + item.id)
	}
	},
	onLoad () {
		uni.startPullDownRefresh()
	}
}
